body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1024px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

body {
  box-sizing: border-box;
}

.debug {
  height: 100vh;
  position: fixed;
  width: 100%;
}

.debug > div {
  height: inherit;
  max-width: 1024px;
  padding-left: 30px;
  padding-right: 30px;
  margin: 0 auto;
}

.debug > div > div {
  height: inherit;
  display: flex;
  flex-wrap: wrap;
  margin-left: -7.5px;
  margin-right: -7.5px;
}

.debug > div > div > div {
  box-sizing: border-box;
  margin-left: 7.5px;
  margin-right: 7.5px;
  word-wrap: break-word;
  width: calc(4.166666666666667% - 15px);
  height: inherit;
  outline: 1px dotted #a69ed4;
  background: rgba(0, 0, 0, 0.1);
}

.main .wrapper .product {
  margin-top: 30px;
  background: #494747;
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -7.5px;
  margin-right: -7.5px;
}

.main .wrapper .product .productItem {
  background: white;
  margin: 20px 0;
  min-height: 300px;
  display: flex;
  flex-wrap: wrap;
  margin-left: -7.5px;
  margin-right: -7.5px;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  margin-left: 7.5px;
  margin-right: 7.5px;
  word-wrap: break-word;
  width: calc(25% - 15px);
}

.main .wrapper .product .productItem__img {
  padding: 10px;
}

.main .wrapper .product .productItem__subtatle {
  padding: 5px;
}

.main .wrapper .product .productItem__price {
  padding: 5px;
}

.main .wrapper .product .productItem__price-sale {
  background: yellow;
  padding: 5px;
  border: 1px solid rgba(0, 0, 0, 0.082);
  border-radius: 10px;
  transition: all 0.5s;
}

.main .wrapper .product .productItem__price-sale:hover {
  opacity: 0.5;
  background: #ffbb00;
}

.main .wrapper .product .productItem__btn {
  display: flex;
  justify-content: center;
  width: 50%;
  margin-top: auto;
  margin-bottom: 20px;
  background: #dd5d36;
  padding: 5px;
  border-radius: 5px;
  color: white;
}

.main .wrapper .product .productItem__btn:hover {
  cursor: pointer;
}

.main .wrapper .product .productItem__btn-loading {
  display: flex;
  justify-content: center;
  width: 50%;
  margin-top: auto;
  margin-bottom: 20px;
  background: #7a665f;
  padding: 5px;
  border-radius: 5px;
  color: white;
}

.main .wrapper .product .productItem__btn-done {
  display: flex;
  justify-content: center;
  width: 50%;
  margin-top: auto;
  margin-bottom: 20px;
  background: #0ce7b0;
  padding: 5px;
  border-radius: 5px;
  color: white;
}

